/* Primary color */
:root {
    --primaryColor: #ffcc17ff;
}

/* Fonts */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&family=Rubik+Dirt&display=swap");

/* Header (Navbar + Main display) */
#header {
    width: 100%;
    height: 95vh;
    background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.9)), url(../Header/images/header__bg.jpg);
        background-size: cover;
        background-position: center;
        background-repeat: none;
}

/* Header Text */
.header__text {
    margin-top: 20%;
    font-size: 30px;
}

.header__text h1 {
    margin-top: 20px;
    font-size: 60px;
}

.animated__text{
    display: flex;
    flex-direction: row;
}

.animated__text > * {
    margin-left: 5px;
    align-items: center;
    justify-content: center;
}

.header__text  p{
    color: var(--primaryColor);
    width: 0ch;
    overflow: hidden;
    white-space: nowrap;
    animation: text 6s  infinite;
}

/* Animation */
@keyframes text {
    0%{
        width: 0ch;
    }
    50%{
        width: 20ch;
    }
}

.header__text p span {
    color: var(--primaryColor);
}


/* Making responsive for small screens */
@media only screen and (max-width:600px) {
    #header {
        background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.9)), url(../Header/images/mob__bg.jpg);
            background-size: cover;
            background-position: center;
            background-repeat: none;
    }

    .header__text {
        margin-top: 100%;
        font-size: 16px;
    }

    .header__text h1 {
        margin-top: 20px;
        font-size: 30px;
    }


}